	module.exports = {"data":{"post":{"html":"<p>It's been one of those weeks, or actually, one of those two weeks (bi-weeks?). It's budget season for Mama, so she's had 6 day weeks and is gone more often than not, and is sometimes gone even when she's here. Meanwhile, while Christopher has surely gotten into the rhythm of his kindergarten schedule, I most certainly have not. When Mama is gone I've got 6 hours of non-stop Christopher, which is more exhausting (and infinitely more satisifying) than my 6 hours of work.</p>\n<p>I was hoping I could at least be semi-productive during my extra hours with him, but it turns out my productivity when we are alone is actually zero. He's loud, assertive, and always wanting to communicate. That's very good for his speech development, but very bad for concentrating on anything else in his presence. </p>\n<p>To top things off, Christopher was home sick from kindergarten the past two days. Yesterday, Mama stayed home with him, and he slept with a fever most of the day. Today, I was home; he woke up at 6 am, at 8 am he declared himself for healthy and he has been moving at full speed ever since. Another non-productive day, although it's not like I'm missing anything at work.</p>","fields":{"title":"There He Goes Again","date":"10 Sep 2004","url":"https://papascott.de/2004-09-10-there-he-goes-again/"}}},"pathContext":{"slug":"/2004-09-10-there-he-goes-again/","prev":{"fields":{"title":"Plagiarizers of the world unite!","date":"10 Sep 2004","slug":"/2004-09-10-plagiarizers-of-the-world-unite/"}},"next":{"fields":{"title":"Enough For Now","date":"11 Sep 2004","slug":"/2004-09-11-enough-for-now/"}}}}

